<template>
    <div class="menu-item">
        <el-menu-item :index="menu.path" v-if="menu.children.length === 0">
			<fa-icon class="icon" :icon="menu.icon"></fa-icon>
            <span slot="title" style="padding-left:5px;">{{menu.title}}</span>
        </el-menu-item>
        <el-submenu :index="menu.path" v-else :class="{'menu':isCollapse}">
            <template slot="title">
				<fa-icon class="icon" :icon="menu.icon"></fa-icon>
                <span slot="title" style="padding-left:5px;">{{menu.title}}</span>
            </template>
            <menu-item v-for="(subMenu,key) in menu.children" :key="key" :menu="subMenu"></menu-item>
        </el-submenu>
    </div>
</template>

<script>
	export default {
		name: 'MenuItem',
		props: {
			menu: {
				type: Object,
				required: true,
				default() {
					return {};
				}
			}
		},
		computed: {
			isCollapse() {
				return this.$store.state.isCollapse;
			},
		},
	};
</script>

<style rel="stylesheet/scss" type="text/scss" lang="scss">
    .menu-item {
        .icon {
            color: #fff!important;
            font-size: 16px;
        }
        .menu .el-submenu__title span,
        .menu .el-submenu__title .el-submenu__icon-arrow{
            display: none;
        }
    }

</style>
