import Vue from 'vue';
import VueRouter from 'vue-router';
import Layout from '../views/components/layout/index.vue';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Layout,
    },
];

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes,
});

router.beforeEach((to, from, next) => {
    next();
});

export default router;
